import { reportesData } from '../services/ReportsService';
import XLSX from 'xlsx';

export function exportarDatosAExcel(consulta, codempresa) {
  return new Promise((resolve, reject) => {
    reportesData(consulta.cliente, consulta.cisterna, consulta.fechaDesde, consulta.fechaHasta, codempresa)
      .then(response => {
        const dataExportar = response.data.data.map((resultado) => ({
          Ticket: resultado.idticket.toString(), // Formatea "Ticket" como nmero
          Cisterna: resultado.codcisterna,
          Nombre: resultado.nombre,
          Cliente: resultado.cliente,
          Detalle: resultado.detalle,
          Conductor: resultado.conductor,
          Equipo: resultado.equipo,
          Facturacin: resultado.facturacion,
          Fecha: resultado.fecha.split(" ")[0],
          Hora: resultado.fecha.split(" ")[1],
          Galones: parseFloat(resultado.galones), // Convierte "Galones" a nmero
          Horometro: resultado.horometro,
          Odometro: resultado.odometro,
          Punto: resultado.punto,
          Recibido_por: resultado.recibido,
          Guia: resultado.guia
        }));
        // Crear una hoja de clculo de Excel
        const workbook = XLSX.utils.book_new();
        // Convertir la matriz de datos a una hoja de clculo de Excel
        const worksheet = XLSX.utils.json_to_sheet(dataExportar);
        // Agregar la hoja de clculo al libro de trabajo
        const sheetName = 'Datos';
        XLSX.utils.book_append_sheet(workbook, worksheet, sheetName);

        // Guardar el archivo de Excel
        XLSX.writeFile(workbook, 'reporte.xlsx', { bookType: 'xlsx', type: 'buffer' });

        resolve();
      })
      .catch(error => {
        reject(error);
      });
  });
}
